import React from "react"
import Layout from "./Layout";
import {graphql, Link} from "gatsby";
import {SEO} from "theme-seo";
import GetPersianDate from "./JalaliDate";
import {GetBackendUrl} from "theme-library"

const Invoice = ({data, pageContext}) => {
    let parseServerUrl = pageContext.parseServerUrl;
    const backendUrl = GetBackendUrl();
    if(backendUrl !== null)
        parseServerUrl = backendUrl
    const [product, setProduct] = React.useState({IsSelectedOption1: false, IsSelectedOption2: false,Option1Count:0, Option2Count:false });
    const [result, setResult] = React.useState({Total: "", Option1Total: "0", Option2Total: "0"});
    const [options, setOptions] = React.useState([]);

    const claims = getCookie("Claims");
    const user = claims ? JSON.parse(decodeURIComponent(claims)) : null

    React.useEffect(function () {

        fetch(parseServerUrl + 'parse/classes/Option', {
                headers: {
                    'X-Parse-Application-Id': `${pageContext.applicationId}`
                }
            }
        ).then(function (response) {
            return response.json();
        }).then(function (r) {
            const m = r.results.map(option => {
                option.IsSelected = false
                return option;
            })
            setOptions(m);
        });

        let selectedProduct = window.localStorage.getItem('selectedProduct');
        let selectedProductObject = JSON.parse(selectedProduct);
        if(selectedProductObject){
            setProduct(selectedProductObject)
            window.localStorage.removeItem('selectedProduct');
            CalculateTotalPrice(selectedProductObject)
        }
        else
            {
                let newProduct = pageContext.product;
                let imageNode = data.allProductsImages.nodes.filter(p => p.name === newProduct.objectId)[0];
                let mdNode = data.allMarkdownRemark.nodes.filter(p => p.frontmatter.productId === newProduct.objectId)[0];
                newProduct.Name = mdNode.frontmatter.title;
                newProduct.Image = imageNode.childImageSharp ? imageNode.childImageSharp.fixed.src : null;
                newProduct.TotalTax = newProduct.DiscountedPrice * newProduct.Tax / 100;
                newProduct.IncludedOptions = mdNode.frontmatter.IncludedOptions ?? null;
                newProduct.Option1Key = mdNode.frontmatter.Option1Key;
                newProduct.Option1 = mdNode.frontmatter.Option1;
                newProduct.Option1Multi = mdNode.frontmatter.Option1Multi;
                newProduct.Option2Key = mdNode.frontmatter.Option2Key;
                newProduct.Option2 = mdNode.frontmatter.Option2;
                newProduct.Option2Multi = mdNode.frontmatter.Option2Multi;

                newProduct.IsSelectedOption1 = false;
                newProduct.Option1Count = mdNode.frontmatter.Option1Multi ? 0 : 1;
                newProduct.IsSelectedOption2 = false;
                newProduct.Option2Count = mdNode.frontmatter.Option2Multi ? 0 : 1;
                setProduct(newProduct)
                CalculateTotalPrice(newProduct)
            }
    }, []);

    function NumberWithCommas(x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    const [buttonState, setButtonState] = React.useState({state: true, buttonStyle: "cursor-not-allowed"});

    function CalculateTotalPrice(p) {
        const invoice = {ProductId: p.objectId}
        if (p.IsSelectedOption1) {
            invoice[p.Option1Key] = p.Option1Multi ? p.Option1Count : true;
        }
        if (p.IsSelectedOption2) {
            invoice[p.Option2Key] = p.Option2Multi ? p.Option2Count : true;
        }
        invoice["Options"] = options.filter(m => m.IsSelected).map(m => m.objectId).join();
        fetch(parseServerUrl + 'parse/functions/calc', {
            method: 'POST',
            headers: {
                'X-Parse-Application-Id': `${pageContext.applicationId}`,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({'invoice': invoice})
        }).then(function (response) {
            return response.json();

        }).then(function (response) {
            console.log(response.result)
            setResult(response.result);
        })
    }

    function SelectOptionKey(event) {
        let newProduct = {...product};
        let checked = event.target.checked;
        newProduct["IsSelected" + event.target.name] = checked;
        setProduct(newProduct)
        CalculateTotalPrice(newProduct);

    }

    function SelectOption(event) {
        let checked = event.target.checked;
        let compyOptions = [...options]
        compyOptions = compyOptions.map(option => {
                if(option.Title === event.target.name)
                    option.IsSelected = checked
                return option;
            })

        setOptions(compyOptions)
        let newProduct = {...product};
        newProduct["Options"] = compyOptions;
        CalculateTotalPrice(newProduct);
    }

    function SetOptionCounts(event) {
        let newProduct = {...product};
        newProduct[event.target.name + "Count"] = event.target.value;
        newProduct["IsSelected" + event.target.name] = event.target.value !== "0";
        setProduct(newProduct);
        CalculateTotalPrice(newProduct);
    }

    function EnableButton(event) {
        let bState = {state: !event.target.checked}
        if (bState.state) {
            bState.buttonStyle = "cursor-not-allowed"
        } else {
            bState.buttonStyle = "opacity-100 hover:bg-green-600"
        }

        setButtonState(bState);
    }

    function CreateSelectItems() {
        let items = [];
        for (let i = 0; i <= 10; i++) {
            items.push(<option key={i} value={i}>{i}</option>);
        }
        return items;
    }

    function getCookie(name) {
        if (typeof document === 'undefined')
            return;
        const value = `; ${document.cookie}`;
        const parts = value.split(`; ${name}=`);
        if (parts.length === 2) return parts.pop().split(';').shift();
    }

    function SetProductInLocalStorage() {
        window.localStorage.setItem('selectedProduct', JSON.stringify(product));
    }

    function CreateInvoice() {
        if(!user){
            SetProductInLocalStorage();
            window.location = pageContext.authUrl + "?returnUrl=" + window.location;
            return;
        }

        if ((user && !user.CodeEghtesadi && !user.CodeMelli) ||
            (user && user.CodeMelli &&( !user.Ostan || !user.Shahr || !user.Senf || !user.Name || !user.FamilyName || !user.MobileNumber ) )||
            (user && !user.CodeMelli && user.CodeEghtesadi && (!user.ShenaseMelli || !user.Address || !user.CodePosti)))
        {
            SetProductInLocalStorage();
            window.location = window.location.origin + "/profile?returnUrl=" + window.location;
            return;
        }

        let option1Key = product.Option1Key;
        let option1Value;
        if (product.IsSelectedOption1) {
            option1Value = product.Option1Multi ? parseInt(product.Option1Count) : true;
        } else
            option1Value = product.Option1Multi ? 0 : false;

        let option2Key = product.Option2Key;
        let option2Value;
        if (product.IsSelectedOption2) {
            option2Value = product.Option2Multi ? parseInt(product.Option2Count) : true;
        } else
            option2Value = product.Option2Multi ? 0 : false;

        function SaveInvoice(shamsiDate) {
            const transaction = Sentry.startTransaction({ name: "Payment" });
            let span = transaction.startChild({ op: "CreateInvoice" }); // This function returns a Span
            const date = new Date().toISOString().slice(0, 10);
            let invoice = {
                ProductId: product.objectId,
                Tarikh: shamsiDate,
                ProductName: product.Name,
                UserID: user.ParseUser,
                Date: date,
                "ACL": {
                    [user.ParseUser]: {
                        "read": true,
                        "write": true
                    }
                }
            }

            if(product.Option1Key)
                invoice[option1Key] = option1Value;

            if(product.Option2Key)
                invoice[option2Key] = option2Value;

            invoice.Options = options.filter(m => m.IsSelected).map(m => m.objectId).join(",")

            fetch(parseServerUrl + 'parse/classes/Invoice', {
                method: 'POST',
                headers: {
                    'X-Parse-Application-Id': `${pageContext.applicationId}`,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(invoice)
            }).then(function (response) {
                return response.json();
            }).then(function (result) {
                span.finish();
                span = transaction.startChild({ op: "Pay" });
                const validUsers = ['G10w7XwAXF', '5uJhGYbQUs', 'BhQk7FiTC6']
                const amount = (window.location.hostname === "gatsby.raymasoft.com" || window.location.hostname === "gatsby.rayvarz.studio"
                    || window.location.hostname === "test.rayvarz.studio" || window.location.hostname === "latest.raymasoft.com"
                    || window.location.hostname === "localhost") || validUsers.includes(user.ParseUser) ? "1000" : result["Amount"] * 10;
                const redirectUrl = window.location.origin + "/profile";
                const invoiceId = pageContext.parseServerPrefix ? pageContext.parseServerPrefix + ':' + result["objectId"] : result["objectId"];
                fetch(pageContext.paymentServerUrl + "Pay?redirectUrl=" + redirectUrl + "&amount=" + amount + "&invoiceId=" + invoiceId + "&flags=GenerateLicense,EnableLicense").then(function (response) {
                    return response.json()
                }).then(function (response) {
                    span.finish();
                    transaction.finish();
                    document.getElementById("amount").value = amount;
                    document.getElementById("reserveNum").value = invoiceId;
                    document.getElementById("redirectUrl").value = redirectUrl;
                    document.getElementById("token").value = response["token"];
                    document.getElementById("form").submit();
                }).catch(function (){
                    span.finish()
                    transaction.finish();
                })
            })
        }

        const persianDate = GetPersianDate(new Date());
        SaveInvoice(persianDate)
    }

    return (
        <Layout>
            <SEO title={pageContext.title}/>
            {product.Name && <div className="my-8 p-2 w-11/12 lg:w-3/4 mx-auto">
                <div className="lg:flex" key={product.Name}>
                    {product.Image ?
                        <img className={"m-8 mr-0 h-48"} width="350" height="150"
                             src={product.Image} alt={product.Name}/> : null}
                    <table key={product.Name}
                           className="mx-auto text-right border-collapse text-gray-700 lg:w-2/3">
                        <thead>
                        <tr className="py-4 px-6 border-b-4 border-teal-500">
                            <th>مشخصات محصول</th>
                            <th>قیمت(تومان)</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr className="py-4 px-6 border-b border-gray-300">
                            <td>{product.Name}</td>
                            {product.Price !== product.DiscountedPrice
                            ? <td className="line-through text-center">{NumberWithCommas(product.Price)}</td>
                            : <td className="text-center">{NumberWithCommas(product.Price)}</td>
                            }
                        </tr>
                        {product.DiscountedPrice === product.Price ? null :
                        <tr className="py-4 px-6 border-b border-gray-300">
                            <td>قیمت با تخفیف</td>
                            <td className="text-center">{NumberWithCommas(product.DiscountedPrice)}</td>
                        </tr>
                        }
                        {product.IncludedOptions ? <tr className="py-4 px-6 border-b border-gray-300">
                            <td>{product.IncludedOptions}</td>
                            <td className="text-center">رایگان</td>
                        </tr> : null
                        }

                        {product.Option1Key ?
                            <tr className="py-4 px-6 border-b border-gray-300">
                                <td>
                                    {!product.Option1Multi ?
                                        <input type="checkbox"  checked={product.IsSelectedOption1} onChange={SelectOptionKey}
                                               name={"Option1"} className={"regular-checkbox"}/> : null}
                                    <label className="mr-1 align-top">{product.Option1}</label>
                                    {product.Option1Multi ? <select type="number"
                                                                    className="border-2 border-gray-400 w-12 mr-8 ml-2"
                                                                    name={"Option1"}
                                                                    value={product.Option1Count}
                                                                    onChange={SetOptionCounts}
                                        >
                                            {CreateSelectItems()}
                                        </select>
                                        : null}
                                </td>
                                <td className="text-center">{NumberWithCommas(result.Option1Total)}</td>
                            </tr>
                            : null}
                        {product.Option2Key ?
                            <tr className="py-4 px-6 border-b border-gray-300">
                                <td>
                                    {!product.Option2Multi ? <input type="checkbox" checked={product.IsSelectedOption2}
                                                                    name={"Option2"} onChange={SelectOptionKey}
                                                                    className="regular-checkbox"/> : null
                                    }
                                    <label className="mr-1 align-top">{product.Option2}</label>
                                    {product.Option2Multi ? <select type="number"
                                                                    className="border-2 border-gray-400 w-12 mr-8 ml-2"
                                                                    name={"Option2"}
                                                                    value={product.Option2Count}
                                                                    onChange={SetOptionCounts}>
                                            {CreateSelectItems()}
                                        </select>
                                        : null}
                                </td>
                                <td className="text-center">{NumberWithCommas(result.Option2Total)}</td>

                            </tr>
                            : null}
                        {options && options.length > 0 && <tr className="py-4 px-6 border-b border-gray-300"> <td>
                            <label className="mr-1 align-top">امکانات جانبی</label>
                            <div className="flex flex-col">
                                {options.map(option => (
                                    <>
                                        <td className={"mr-6"}>
                                            <input type="checkbox" key={option.objectId} checked={option.IsSelected}
                                                   name={option.Title} onChange={SelectOption}
                                                   className="regular-checkbox"/>
                                            <label className="mr-1 align-top">{option.Title}</label>
                                            <label className="mr-1 align-top font-bold"> ({NumberWithCommas(option.Price)} تومان)</label>

                                        </td>
                                    </>

                                ))} </div></td>
                            <td className="text-center"> {NumberWithCommas(options.filter(m => m.IsSelected).map(m => m.Price).reduce((a, b) => a + b, 0))} </td>
                        </tr>}
                        </tbody>

                        {result.Total && <tfoot className="my-6">
                        <tr className="py-4 px-6 border-b border-gray-300">
                            <td>مجموع (با احتساب مالیات بر ارزش افزوده)</td>
                            <td className="font-bold text-center">{NumberWithCommas(result.Total)}</td>
                        </tr>
                        </tfoot>}

                    </table>
                </div>

                <form id="form" action="https://pna.shaparak.ir/_ipgw_/payment/" method="post">
                    <input type="hidden" id="token" name="token" />
                    <input type="hidden" id="language" name="language" value="fa" />
                    <input type="hidden" id="transType" name="transType" value="enGoods" />
                    <input type="hidden" id="reserveNum" name="reserveNum" />
                    <input type="hidden" id="amount" name="amount" />
                    <input type="hidden" id="redirectUrl" name="redirectUrl" />
                </form>

                <div className="my-10">
                    <div className="lg:pr-64 lg:mr-20">
                        <input type="checkbox" name="licence" onChange={EnableButton} className="regular-checkbox"/>
                        <label className="mr-1 align-top">تمامی بند های <Link to="/legal"
                                                                              className="text-teal-600 font-bold">قوانین
                            و مقررات</Link> را قبول دارم.</label>
                    </div>
                    <div>
                        <button disabled={buttonState.state}
                                className={"bg-teal-600 text-white opacity-50 font-bold py-2 px-4 rounded-lg my-10 float-left " + buttonState.buttonStyle}
                                type="button" onClick={CreateInvoice}>ادامه فرایند خرید
                        </button>
                    </div>
                </div>
            </div>}
        </Layout>
    )
}

export default Invoice;

export const query = graphql`
 {
 allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/product/"  }})
   {
    nodes {
      fields {
        slug
      }
      frontmatter {
        title
        productId
        IncludedOptions
        Option1Key
        Option1
        Option1Multi
        Option2Key
        Option2
        Option2Multi
      }
    }
  }
  allProductsImages: allFile(filter: { sourceInstanceName: { eq: "product" } }) {
    nodes {
      name
      childImageSharp{
          fixed(width:500){
             src
          }
       }
    }
  }
}
`;